.bunner {
  overflow: hidden;
  min-height: 100vh;

  &__slider {
    position: relative;

    &_video {
      display: block;
      position: absolute;
      min-width: 100vw;
      min-height: 100vh;
    }

    &_container {
      padding: 33.33vh 23.75vw 10.35vh;
      flex-direction: column;
      display: flex;
      justify-content: space-between;
      height: -webkit-fill-available;

    }

    &_content {
      font-family: "Poppins", sans-serif;
      font-weight: 600;
      color: #fff;
      display: flex;
      flex-direction: column;
      align-items: center;
      z-index: 1;

      &_header {
        font-size: 90px;
        line-height: 100px;
        text-align: center;
        margin-bottom: 25px;
        z-index: inherit;
      }

      &_text {
        font-size: 18px;
        line-height: 32px;
        text-align: center;
        margin-bottom: 54px;
        letter-spacing: 0.2px;
        z-index: inherit;
      }

      &_button {
        width: 172px;
        height: 54px;
        background-color: #00a031;
        border-radius: 27px;
        color: #f4fbf6;
        font-family: "Gilroy", sans-serif;
        font-size: 18px;
        font-weight: 700;
        line-height: 22px;
        letter-spacing: 0.1px;
        margin-bottom: 54px;
        cursor: pointer;
        z-index: inherit;
      }
    }

    &_button-left {
      height: 44px;
      width: 24px;
      position: absolute;
      top: 44.53vh;
      left: 8.85vw;
      background-image: url(./../images/Vector_left.svg);
      background-repeat: no-repeat;
      background-position: center;
      background-color: rgba(255, 255, 255, 0);

      &:hover {
        cursor: pointer;
      }
    }

    &_button-right {
      height: 44px;
      width: 24px;
      position: absolute;
      top: 44.53vh;
      left: 91.15vw;
      background-image: url(./../images/Vector_right.svg);
      background-repeat: no-repeat;
      background-position: center;
      background-color: rgba(0, 0, 0, 0);

      &:hover {
        cursor: pointer;
      }
    }

    &_dots-container {
      width: 70px;
      display: flex;
      justify-content: space-between;
      margin-left: auto;
      margin-right: auto;
      z-index: 1;
      align-items: center;
    }

    &_dot {
      width: 13px;
      height: 13px;
      background-color: #fff;
      border-radius: 50%;
      z-index: inherit;

      &_active {
        border: 1px solid #00a031;
        border-radius: 50%;

        & > div {
          background-color: #00a031;
          margin: 3px;
          width: 13px;
          height: 13px;
          border-radius: 50%;
        }
      }
    }
  }
}

@media screen and (max-width: 1024px) {
  .bunner {
    &__slider {

      &_container {
        padding: 15vh 23.75vw 10.35vh;
      }

      &_content_header {
        font-size: 60px;
        line-height: 80px;
      }

      &_dots-container {
        display: none;
      }
    }
  }
}

@media screen and (max-width: 767px) {
  .bunner {
    &__slider {

      &_container {
        padding: 25vh 5vw;
      }

      &_content_header {
        font-size: 35px;
        line-height: 40px;
      }

      &_button-left {
        display: none;
      }

      &_button-right {
        display: none;
      }
    }
  }
}
